// @require 'node_modules/highlight.js/styles/atom-one-light.css'
import React, { Component, PropTypes } from 'react';
import Tab, { TabPanel } from '../../components/tab/index';
import reactElementToString from 'react-element-to-jsx-string';
import { highlight } from 'highlight.js';
import { displayName } from './getDisplayName';

export class DemoPanel extends Component {
  static defaultProps = {
    title: 'Demo 标题'
  };

  static propTypes = {
    title: PropTypes.node
  };

  constructor(props) {
    super(props);

    this.renderCode = this.renderCode.bind(this);
  }

  renderCode() {
    if (!this.codeObj) {
      let children = this.props.children;
      children = children.length ? <div>{children}</div> : children;
      let highLightCode = reactElementToString(children, {
        displayName,
        showDefaultProps: false
      });
      this.codeObj = highlight('javascript', `render() {\n return (\n\n${highLightCode}\n\n);\n}`);
    }

    return {
      __html: this.codeObj.value
    };
  }

  render() {
    return (
      <div className="imui-demo-panel">
        <h2> ▼ {this.props.title}</h2>
        <Tab tabType="top">
          <TabPanel tab="Demo">
            {this.props.children}
          </TabPanel>
          <TabPanel tab="Source">
            <pre>
              <code className="hljs atom-one-light"
                dangerouslySetInnerHTML={this.renderCode()}
              ></code>
            </pre>
          </TabPanel>
        </Tab>
      </div>
    );
  }
}
